import React, { Component } from "react";
import Card from "react-bootstrap/Card";

class Music extends Component {
  state = {};
  render() {
    return (
      <div id="music" className="section">
        <h1>Music</h1>
        <p>
          One of my hobbies is playing the piano. Here's a taste of my music!
        </p>
        <div className="d-flex justify-content-center">
          <Card className="music-card">
            <Card.Body>
              <Card.Title>For the First Time in Forever</Card.Title>
              <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/AWkB_Twkkjs"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen
              ></iframe>
            </Card.Body>
          </Card>

          <Card className="music-card">
            <Card.Body>
              <Card.Title>Super Mario Theme</Card.Title>
              <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/QdZfU04EudE"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen
              ></iframe>
            </Card.Body>
          </Card>
        </div>
      </div>
    );
  }
}

export default Music;
